.header{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}

.header__menu-btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 30px;
  height: 30px;
  margin-right: 15px;
  cursor: pointer;
}

.header__menu-btn div {
  background-color: #ffffff;
  height: 3px;
  width: 100%;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.header__menu-btn div:nth-child(2) {
  background-color: #b3a577;
}

.header__list.show + .header__menu-btn div:first-child {
  -webkit-transform: translateX(-9px) rotate(-45deg) ;
      -ms-transform: translateX(-9px) rotate(-45deg) ;
          transform: translateX(-9px) rotate(-45deg) ;
  width: 125%;
  -webkit-transform-origin: right bottom ;
      -ms-transform-origin: right bottom ;
          transform-origin: right bottom ;
  background-color: #b3a577;
} 

.header__list.show + .header__menu-btn div:nth-child(2) {
  display: none;
}

.header__list.show + .header__menu-btn div:last-child {
  -webkit-transform: translateX(-9px) rotate(45deg);
      -ms-transform: translateX(-9px) rotate(45deg);
          transform: translateX(-9px) rotate(45deg);
  width: 125%;
  -webkit-transform-origin: right  top;
      -ms-transform-origin: right  top;
          transform-origin: right  top;
  background-color: #b3a577;
} 

.header__list.show {
  -webkit-transform: scaleX(1) translateX(0%);
      -ms-transform: scaleX(1) translateX(0%);
          transform: scaleX(1) translateX(0%);
  opacity: 1;
  visibility: visible;
}


.header__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  opacity: 0;
  -webkit-transform: scaleX(0) translateX(0%);
      -ms-transform: scaleX(0) translateX(0%);
          transform: scaleX(0) translateX(0%);
  -webkit-transform-origin: right center;
      -ms-transform-origin: right center;
          transform-origin: right center;
  visibility: hidden;
}

.header__link {
  color: #ffffff;
  font-size: 24px;
  line-height: 24px;
  text-decoration: none;
  margin-left: 15px;
  margin-right: 15px;
  -webkit-transition: ease-in 300ms;
  -o-transition: ease-in 300ms;
  transition: ease-in 300ms;
  padding-bottom: 5px;
}

.header__link:hover,
.header__link:focus {
  color: #b8b098;
}

.header__link-active {
  color: #b3a577;
  font-weight: bold;
  position: relative;
  
}

.header__link-active::after {
  content: '';
  position: absolute;
  width: 50px;
  height: 2px;
  bottom: 0;
  left: 0;
  background: #b3a577;
  background: -webkit-gradient(linear, left top, right top, from(#b3a577), color-stop(50%, #b8b098));
  background: -o-linear-gradient(left, #b3a577 0%, #b8b098 50%);
  background: linear-gradient(90deg, #b3a577 0%, #b8b098 50%);
}

@media (max-width: 600px) {
  .header {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
  }

  .header__menu-btn {
    margin: 10px auto 15px;
  }

  .header__list.show {
    -webkit-transform: scaleY(1) translateY(0%);
        -ms-transform: scaleY(1) translateY(0%);
            transform: scaleY(1) translateY(0%);
    opacity: 1;
    max-height: 300px;
    visibility: visible;
  }

  .header__list {
    text-align: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    max-height: 0;
    -webkit-transform: scaleY(0) translateY(0%);
        -ms-transform: scaleY(0) translateY(0%);
            transform: scaleY(0) translateY(0%);
    -webkit-transform-origin: top center;
        -ms-transform-origin: top center;
            transform-origin: top center;
    visibility: hidden;
  }

  .header__list li {
    margin-bottom: 15px;
  }

  .header__link {
    font-size: 32px;
  }
}