.layout {
  background-color: #3d3d3d;
  color: #ffffff;
  margin: 15px;
  padding: 25px 75px;
}

.beta {
  position: fixed;
  right: 25px;
  bottom: 25px;
}

@media (max-width: 560px) {
  .layout {
    padding: 25px;
  }

  .beta {
    left: 25px;
    top: 15px;
    right: auto;
    bottom: auto;
  }
}