.home-top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.home-top__left {
  max-width: 550px;
}

.home-top__img { 
  width: 100%;
  border-radius: 50%;
  border: 3px solid #b8b098;
}

.home-top__right {
  max-width: 550px;
  margin-left: 20px;
}

.home-top__title {
  margin: 30px 0;
  font-size: 42px;
}

.home-top__text {
  margin: 30px 0;
  font-size: 36px;
  -webkit-transition: ease-out 300ms;
  -o-transition: ease-out 300ms;
  transition: ease-out 300ms;
}

.home-top__text:hover {
  color: #b3a577;
}

.home-bottom {
  max-width: 1000px;
  margin: 40px auto;
  text-align: center;
}

.home-bottom__title {
  font-size: 32px;
}

.home-bottom__text {
  margin-top: 20px;
  font-size: 24px;
  letter-spacing: .6px;
  line-height: 40px;
}

.r-it {
  color: #2bcdff;
  font-weight: bold;
}

.r-symbol {
  display: inline-block;
  -webkit-transition: linear .5s;
  -o-transition: linear .5s;
  transition: linear .5s;
}

.r-it:hover .r-symbol {
  -webkit-transform: rotateX(-360deg);
          transform: rotateX(-360deg);
  color: #fff;
}

.a-it {
  color: #ff4e4e;
  font-weight: bold;
}

.a-symbol {
  -webkit-transition: ease-out .5s;
  -o-transition: ease-out .5s;
  transition: ease-out .5s;
}

.a-symbol:hover {
  color: #fff;
}

.v-it {
  color: #398a53;
  font-weight: bold;
}

.v-symbol {
  display: inline-block;
  -webkit-transition: linear .5s;
  -o-transition: linear .5s;
  transition: linear .5s;
}

.v-it:hover .v-symbol {
  -webkit-transform: rotateX(720deg);
          transform: rotateX(720deg);
  color: #fff;
}

@media (max-width: 1200px) {
  .home-top {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
  }

  .home-top__right {
    margin: 0;
    text-align: center;
    max-width: 700px;
  } 
}

@media (max-width: 600px) {
  .home-top__title {
    margin-top: 0;
    font-size: 32px;
  } 

  .home-top__text {
    font-size: 24px;
  }

  .home-bottom__text {
    font-size: 16px;
    letter-spacing: 0px;
    line-height: 26px;
  }

  .r-it,
  .a-it,
  .v-it {
    font-weight: normal;
  }
}