.projects__buttons {
  text-align: center;
}

.projects__buttons button {
  outline: none;
  -webkit-transition: ease-in .3s;
  -o-transition: ease-in .3s;
  transition: ease-in .3s;
  font-size: 24px;
  padding: 1rem 2rem;
  border-radius: 3px;
}

.projects__button {
  color: #3d3d3d;
  background-color: #ffffff;
  -webkit-box-shadow: 0 -3px  #3d3d3d inset;
          box-shadow: 0 -3px  #3d3d3d inset;
  cursor: pointer;
}

.projects__button:disabled {
  background-color: #b3a577;
  color: #ffffff;
  -webkit-box-shadow: 0 2px  #000000 inset;
          box-shadow: 0 2px  #000000 inset;
  border: none;
  cursor: auto;
}

.projects__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.projects__item {
  width: 25%;
  position: relative;
  padding: 30px;
  margin: 20px 5px;
  border: 1px solid #ffffff;
  -webkit-transition: .3s ease-in;
  -o-transition: .3s ease-in;
  transition: .3s ease-in;
  -webkit-animation: prjctShow .8s;
          animation: prjctShow .8s;
}

.projects__item h3 {
  font-size: 32px;
  margin-bottom: 20px;
}

.projects__item p {
  font-size: 24px;
  margin-bottom: 20px;
}

.project__buttons {
  position: absolute;
  left: 0;
  bottom: 10px;
}

.projects__item a {
  color: #ffffff;
  font-size: 16px;
  text-transform: uppercase;
  padding: 10px 30px;
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  -webkit-transition: ease-in .4s;
  -o-transition: ease-in .4s;
  transition: ease-in .4s;
}

.projects__item a:hover,
.projects__item a:focus {
  background-color: #b3a577;
}

.projects__item span {
  position: absolute;
  right: 10px;
  bottom: 5px; 
  font-size: 24px;
  font-weight: bold;
  line-height: 32px;
  color: #b3a577;
}

@-webkit-keyframes show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes prjctShow {
  0% {
    opacity: .3;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    background-color: #ffffff;
  }

  50% {
    opacity: 0;
    -webkit-box-shadow: 0 0 100px #000;
            box-shadow: 0 0 100px #000;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }

  100% {
    opacity: 1;
    background-color: none;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

@keyframes prjctShow {
  0% {
    opacity: .3;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    background-color: #ffffff;
  }

  50% {
    opacity: 0;
    -webkit-box-shadow: 0 0 100px #000;
            box-shadow: 0 0 100px #000;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }

  100% {
    opacity: 1;
    background-color: none;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

.projects__item:hover {
  border-color: #b3a577;
  -webkit-transform: scale(1.01);
      -ms-transform: scale(1.01);
          transform: scale(1.01);
  -webkit-box-shadow: 0 0 15px rgba(0, 0, 0, .5);
          box-shadow: 0 0 15px rgba(0, 0, 0, .5);
}

.projects__item:hover a{
  border-color: #b3a577;
}

@media (max-width: 1200px) {
  .projects__list {
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  

  .projects__item {
    width: 40%;
  }
}

@media (max-width: 900px) {
  .projects__item {
    width: 70%;
  }
}

@media (max-width: 600px) {
  .projects__buttons button {
    font-size: 16px;
    padding: 10px;
  }

  .projects__item h3 {
    font-size: 20px;
  }

  .projects__item p {
    font-size: 16px;
  }

  .projects__item span {
    top: 5px; 
  }
}