.about-top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  padding-bottom: 40px;
}

.about-top__polaroid {
  max-width: 500px;
  max-height: 700px;
  padding: 15px 15px 10px;
  -webkit-box-shadow: 1px 1px 15px #000000;
          box-shadow: 1px 1px 15px #000000;
}

.about-top__polaroid img {
  max-width: 100%;
  max-height: 100%;
}

.about-top__polaroid figcaption {
  font-weight: bold;
  text-align: center;
  font-size: 24px;
  padding: 30px 15px;
}

.about-top__photos {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.about-top__photo {
  cursor: pointer;
  width: 100px;
  max-height: 115px;
  padding: 5px 5px 15px;
  -webkit-box-shadow: 1px 1px 10px #000000;
          box-shadow: 1px 1px 10px #000000;
}

.about-top__photo:first-child {
  -webkit-transform: translateX(20px) rotate(10deg);
      -ms-transform: translateX(20px) rotate(10deg);
          transform: translateX(20px) rotate(10deg);
}

.about-top__photo:nth-child(2) {
  -webkit-transform: translateY(15px) rotate(-15deg);
      -ms-transform: translateY(15px) rotate(-15deg);
          transform: translateY(15px) rotate(-15deg);
}

.about-top__photo:nth-child(3) {
  z-index: 1;
  -webkit-transform: translateX(-20px) rotate(5deg);
      -ms-transform: translateX(-20px) rotate(5deg);
          transform: translateX(-20px) rotate(5deg);
}

.about-top__photo:last-child {
  -webkit-transform: translate(-40px, 10px) rotate(-45deg);
      -ms-transform: translate(-40px, 10px) rotate(-45deg);
          transform: translate(-40px, 10px) rotate(-45deg);
}

.about-top__photo img {
  width: 100%;
  height: 100%;
}

.about-top__photo figcaption {
  font-size: 6px;
  text-align: center;
  font-weight: bold;
}

figure {
  margin: 10px;
  color: black;
}

.about-title {
  margin-bottom: 20px;
  font-size: 32px;
  text-align: center;
}

.about-title span {
  color: #b3a577;
}

.about-text {
  max-width: 700px;
  font-size: 30px;
  margin-top: 5%;
}

.sertificates {
  margin: 100px auto 10px;
  height: 300px;
  max-width: 1200px;
  text-align: center;
}

.sertificates a {
  display: inline-block;
  max-width: 300px;
  max-height: 200px;
  -webkit-clip-path: polygon(0 0, 0 0, 100% 50%, 0 100%);
          clip-path: polygon(0 0, 0 0, 100% 50%, 0 100%);
  border-left: 5px solid #b3a577;
  margin: 0 -90px;
}

.sertificates a:nth-child(2n+1) {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}

.sertificates a:nth-child(2n) {
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.sertificates img {
  height: 100%;
  width: 100%;
  max-height: 200px;
}

.sertificates a:hover,
.sertificates a:focus {
  border-color: #ffffff;
}

@media (max-width: 1200px) {
  .about-top {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }

  .about-top__polaroid,
  .about-text {
    margin: 15px auto;
  }
}


@media (max-width: 900px) {
  .about-top__photo {
    width: 90px;
    max-height: 95px;
  }

  .about-top__photo figcaption {
    font-size: 4px;
  }

  .about-text {
    text-align: center;
  }

  .sertificates {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: auto;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin-top: 10px;
  }

  .sertificates a {
    display: block;
    margin: 10px;
    padding: 5px;
    border: 5px solid #b3a577;
    max-width: 40%;
    width: 100%;
    -webkit-clip-path: none;
            clip-path: none;
  }

  .sertificates a:nth-child(2n+1) {
    -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
            transform: rotate(0deg); 
  }
  
  
  .sertificates a:nth-child(2n) {
    -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@media (max-width: 600px) {
  .sertificates a {   
    max-width: 80%;
  }

  .about-top {
    padding-bottom: 0;
  }

  .about-text {
    font-size: 18px;
  }

  .about-title {
    font-size: 16px; 
  }

  .about-top__polaroid figcaption {
    padding-left: 0;
    padding-right: 0;
  }
}

@media (max-width: 480px) {
  .about-top__polaroid figcaption {
    font-size: 20px;
    padding: 15px;
  }
}